/* App Layout Styles */
.app-container {
  min-height: 100vh;
  background-color: var(--primary-bg);
  color: var(--primary-text);
}

.main-content {
  padding-top: 70px; /* Account for fixed header */
  margin-left: 280px; /* Account for fixed sidebar */
}

.app {
  display: flex;
  min-height: calc(100vh - 70px);
  background-color: var(--primary-bg);
  color: var(--primary-text);
}

.welcome-message {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  text-align: center;
}

.welcome-message h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: var(--accent-color);
}

.welcome-message p {
  font-size: 1.2rem;
  color: var(--secondary-text);
  margin-bottom: 2rem;
}

.sidebar {
  width: 280px;
  background: var(--secondary-bg);
  color: var(--primary-text);
  padding: 0;
  box-shadow: var(--shadow-lg);
  overflow-y: auto;
  flex-shrink: 0;
  transition: transform 0.3s ease;
  border-right: 1px solid var(--border-color);
  height: calc(100vh - 70px); /* Account for fixed header height */
  position: fixed;
  top: 70px; /* Position below fixed header */
  left: 0;
  z-index: 200; /* Higher than chat-input-bar to ensure sidebar appears above it */
}

.sidebar.collapsed {
  transform: translateX(-100%);
}

/* Responsive sidebar */
@media (max-width: 768px) {
  .sidebar {
    transform: translateX(-100%);
    top: 60px; /* Account for mobile header height */
    height: calc(100vh - 60px);
  }
  
  .sidebar.mobile-open {
    transform: translateX(0);
  }
  
  .main-content {
    margin-left: 0;
    padding-top: 60px; /* Account for mobile header height */
  }
}

/* Additional App-specific styles */
.app {
  position: relative;
}

/* Loading overlay */
.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(4px);
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid var(--border-color);
  border-top: 4px solid var(--accent-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  position: relative;
  z-index: 1001;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Empty state */
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-align: center;
  color: var(--muted-text);
  padding: 40px;
}

.empty-state h3 {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 12px;
  color: var(--primary-text);
}

.empty-state p {
  font-size: 16px;
  line-height: 1.6;
  max-width: 400px;
  color: var(--secondary-text);
}

.empty-state-icon {
  width: 64px;
  height: 64px;
  margin-bottom: 24px;
  opacity: 0.5;
  color: var(--accent-color);
}

/* Error state */
.error-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-align: center;
  color: var(--error-color);
  padding: 40px;
}

.error-state h3 {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 12px;
  color: var(--error-color);
}

.error-state p {
  font-size: 16px;
  line-height: 1.6;
  max-width: 400px;
  color: var(--secondary-text);
  margin-bottom: 20px;
}

.error-state .btn {
  background: var(--error-color);
  color: white;
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.error-state .btn:hover {
  background: #ef4444;
  transform: translateY(-1px);
}

/* Success state */
.success-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-align: center;
  color: var(--success-color);
  padding: 40px;
}

.success-state h3 {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 12px;
  color: var(--success-color);
}

.success-state p {
  font-size: 16px;
  line-height: 1.6;
  max-width: 400px;
  color: var(--secondary-text);
}

/* Form styles */
.form-group {
  margin-bottom: 20px;
}

.form-label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: var(--primary-text);
  font-size: 14px;
}

.form-input {
  width: 100%;
  padding: 12px 16px;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  background: var(--tertiary-bg);
  color: var(--primary-text);
  font-size: 14px;
  transition: all 0.2s ease;
}

.form-input:focus {
  outline: none;
  border-color: var(--accent-color);
  box-shadow: 0 0 0 3px var(--focus-ring);
}

.form-input::placeholder {
  color: var(--muted-text);
}

.form-textarea {
  resize: vertical;
  min-height: 100px;
}

.form-select {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 20 20'%3e%3cpath stroke='%236b7280' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.5' d='m6 8 4 4 4-4'/%3e%3c/svg%3e");
  background-position: right 12px center;
  background-repeat: no-repeat;
  background-size: 16px;
  padding-right: 40px;
}

/* Button group */
.btn-group {
  display: flex;
  gap: 12px;
  align-items: center;
}

.btn-group .btn {
  flex: 1;
}

/* Modal styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(4px);
}

.modal {
  background: var(--secondary-bg);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  padding: 24px;
  max-width: 500px;
  width: 90%;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: var(--shadow-lg);
  animation: modalSlideIn 0.3s ease-out;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 16px;
  border-bottom: 1px solid var(--border-color);
}

.modal-title {
  font-size: 20px;
  font-weight: 600;
  color: var(--primary-text);
}

.modal-close {
  background: none;
  border: none;
  color: var(--muted-text);
  cursor: pointer;
  padding: 8px;
  border-radius: 6px;
  transition: all 0.2s ease;
}

.modal-close:hover {
  background: var(--tertiary-bg);
  color: var(--primary-text);
}

.modal-body {
  margin-bottom: 20px;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  padding-top: 16px;
  border-top: 1px solid var(--border-color);
}

/* Tooltip */
.tooltip {
  position: relative;
  display: inline-block;
}

.tooltip .tooltip-text {
  visibility: hidden;
  width: 200px;
  background-color: var(--tertiary-bg);
  color: var(--primary-text);
  text-align: center;
  border-radius: 6px;
  padding: 8px 12px;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  margin-left: -100px;
  opacity: 0;
  transition: opacity 0.3s;
  border: 1px solid var(--border-color);
  box-shadow: var(--shadow-md);
  font-size: 12px;
}

.tooltip:hover .tooltip-text {
  visibility: visible;
  opacity: 1;
}

/* Badge */
.badge {
  display: inline-flex;
  align-items: center;
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.badge-primary {
  background: var(--accent-color);
  color: white;
}

.badge-secondary {
  background: var(--tertiary-bg);
  color: var(--secondary-text);
  border: 1px solid var(--border-color);
}

.badge-success {
  background: var(--success-color);
  color: white;
}

.badge-warning {
  background: var(--warning-color);
  color: white;
}

.badge-error {
  background: var(--error-color);
  color: white;
}

/* Divider */
.divider {
  height: 1px;
  background: var(--border-color);
  margin: 20px 0;
}

/* Scrollbar styling */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: var(--tertiary-bg);
}

::-webkit-scrollbar-thumb {
  background: var(--border-color);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--border-light);
}

/* Responsive utilities */
@media (max-width: 768px) {
  .sidebar {
    position: fixed;
    left: -100%;
    z-index: 1000;
    height: 100vh;
  }
  
  .sidebar.open {
    left: 0;
  }
  
  .modal {
    margin: 20px;
    width: calc(100% - 40px);
  }
  
  .btn-group {
    flex-direction: column;
  }
}

@media (max-width: 480px) {
  .modal {
    margin: 10px;
    width: calc(100% - 20px);
    padding: 16px;
  }
  
  .modal-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }
}

/* Sidebar login prompt styles */
.mark-history-login-prompt {
  padding: 1rem;
}

.mark-history-login-prompt .placeholder-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  padding: 1rem;
  text-align: center;
  color: var(--secondary-text);
}

.login-prompt-btn {
  background: var(--accent-color);
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 500;
  transition: all 0.2s ease;
  margin-top: 0.5rem;
}

.login-prompt-btn:hover {
  background: var(--accent-hover);
  transform: translateY(-1px);
}

.login-prompt-btn:active {
  transform: translateY(0);
}
